<template>
    <Explorer
      :objects-list-caption="'Projects'|localize"
      :show-selected-object-info="false"
      >
      <template #objectsList>
        <div class="dictionary-parent-list"  v-resize @resize="onResize">

          <TreeView
          id="simple-treeview"
          :create-children="createChildren"
        :root-value="''"
        parent-id-expr="ParentId"
        :expand-nodes-recursive="false"
        data-structure="plain"
        display-expr="Name"
        key-expr="Id"
        :search-enabled="true"
        @item-click="treeViewClick"
    />
        </div>
      </template>
      <template #content>
        <div class="page-container">
          <DataGrid2
          
          :columns="columns"
          :data-source="detailsDataSource"
          :show-column-chooser="true"
          :show-refresh-button="true"
          :show-reset-filters-button="true"
          :show-filter-row="true"
          :show-search-panel="true"
          :show-page-selector="true"
          :show-word-wrap-button="true"
          :word-wrap-enabled="false"
          selection-mode="none"
          :page-size="50"
          :height="gridHeight"
          :allow-grouping="true"
          :allow-column-resizing="true"
          key-expr=""
          />
        </div>
      </template>
    </Explorer>
  </template>
  
  
  <script>
  
  //items-expr="Complects"
  //data-structure="plain"
  //:create-children="createChildren"
  // parent-id-expr="IdContract"
  //root-value="''" 
  //:expand-nodes-recursive="false"
  //:on-item-expanded="onItemExpanded"
  /*
search-mode="startswith"
          selection-mode="single"
          id="simple-treeview"     
          :width="300"  
  */
  import { mapGetters, mapActions } from "vuex";
  import Explorer from "@/components/Common/Explorer.vue";
  //import TreeView from "@/components/Common/TreeView.vue";
  import TreeView from 'devextreme-vue/tree-view';
  import DataGrid2 from "@/components/Common/DataGrid2.vue";
  //import localizeFilter from '@/localize';

  export default {
    components: {
      Explorer,
      TreeView,
      DataGrid2
    },
    methods: {
      ...mapActions([
        "loadContracts",
        "loadComplects",
      
        "loadStage"
      ]),
    
      async createChildren(parent) {
        console.log("parent", parent)
      if (!parent) 
      {
        // возвращает 1-ый уровень
        console.log(1)
        await this.loadContracts();
        console.log(this.getContracts)
        return this.getContracts;

      } 
      else if (parent.itemData.Type=="Contract") 
      {
      
        console.log(parent.itemData.Id)
        await this.loadStage(parent.itemData.Id)
        return this.getStage        
      } 
      else if (parent.itemData.Type=="Stage") 
      {
       
        console.log(parent.itemData.Id)
        await this.loadComplects(parent.itemData.ParentId, parent.itemData.Id)
        return this.getComplects        
      } 
    
      },
     
      async treeViewClick(e) {
          const selected = e.itemData       
          this.detailsDataSource = [selected]
          //console.log(selected.Id)
          //this.loadComplects(selected.Id)
          
      },
      onResize(e) {
          this.gridHeight = e.detail.height;
      },
      /*onItemExpanded(e) {
        console.log(e.itemData.Shifr+"--"+e.itemData.Id)
        this.loadComplects(e.itemData.Id)
        let contract = this.dataSource.find(o=> o.Id==e.itemData.Id)
        contract.Complects = this.getComplects
        //this.setContracts(contract)
      },*/
      
    },
    data() {
      return {
          gridHeight: null,
          detailsDataSource: null,
          dataa: [
    { id: '1', text: 'Fruits', IsGroup: true },     // A root item
    { id: '2', text: 'Vegetables', IsGroup: false }, // Also a root item

]
      }
    },
    computed: {
      ...mapGetters([
        "getContracts",
        "getComplects",
   
        "getStage"
      ]),
      columns() {
      return [{
          dataField: "Shifr",
          width: 200,
          displayExpr: "Shifr"
        }, {
          dataField: "Id",
          width: 200,
          displayExpr: "Id"
        }
      ];
      },
      
      dataSource(){
        //console.log(this.getContracts)
        //return this.getContracts
           return  this.getContracts
      }
    },
    /*
    async mounted() {
    if(this.getContracts.length == 0) {
      await this.loadContracts();
      
    }
    
  }
  */
};
  </script>
  
  <style>
  .dictionary-parent-list {
      flex: 1 1 auto;
      height: 100%;
      display: flex;
      flex-direction: column;
      overflow: hidden;
  }
  </style>